import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CategoryProductComponent } from './category-product/category-product.component';
import { SubCategoryProductComponent } from './sub-category-product/sub-category-product.component';
import { BrandProductComponent } from './brand-product/brand-product.component';
import { ProductMainComponent } from './product-main/product-main.component';
import { ProductSettingComponent } from './product-setting.component';
import { CustomFieldsComponent } from './custom-fields/custom-fields.component';
import { InvoiceReceiptUIComponent } from '../setting-module/invoice-receipt-ui/invoice-receipt-ui.component';

const routes: Routes = [
  {
    path:'',
    component: ProductMainComponent,
    children: [
      {
        path:'',
        redirectTo: 'product-settings',
        pathMatch: 'full'
      },
      { 
        path: "invoice_generate",
        component: InvoiceReceiptUIComponent
      },
      {
        path:'product-settings',
        component: ProductSettingComponent,
        children: [
          {
            path:'',
            redirectTo: 'category_list',
            pathMatch: 'full'
          },
          {
            path:'category_list',
            component: CategoryProductComponent,
          },
          {
            path:'sub_category_list',
            component: SubCategoryProductComponent,
          },
          {
            path:'brand_list',
            component: BrandProductComponent,
          },
          {
            path:'custom-fields',
            component: CustomFieldsComponent,
          },
        ]
      },  
    ],
    pathMatch: 'prefix',
    data: {
      routerLinkActiveOptions: { exact: true, pathMatch: 'full' }
    }
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ProductSettingRoutingModule { }
