
import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { AddBrandComponent } from '../add-brand/add-brand.component';
import { EditBrandComponent } from '../edit-brand/edit-brand.component';
import { Subject, Subscription } from 'rxjs';
import { debounceTime, distinctUntilChanged, tap } from 'rxjs/operators';

@Component({
  selector: 'app-brand-product',
  templateUrl: './brand-product.component.html',
  styleUrls: ['./brand-product.component.scss']
})
export class BrandProductComponent implements OnInit {

  brandForm: any;
  productBrand: any = [];
  iseditBrand: boolean = false;
  brandId: any = [];
  public currentPageNo: number = 0;
  public partyPageSize: number = 5;
  public p = 1;
  public totalrow: any;
  public toatlPages: any;
  public pagesize: number = 1;
  public checkSelectsinglefalse: boolean = false;
  public checkSelectsingle: boolean = false;
  public upiCheckedId = [];
  public checkSingleArray: any = [];
  public isChecked: boolean = false;
  public archiveIconfalse: boolean = false;
  public allSelect: boolean = false;
  public checkSelect: boolean = false;
  private searchSubject = new Subject<string>();
  private searchSubscription: Subscription;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  isLoading: boolean;

  constructor(private formBuilder: FormBuilder, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private productService: ProductService, public dialog: MatDialog,
  ) { }

  ngOnInit(): void {
    this.getAllBrand();
    this.productService.productAdded$.subscribe(() => {
      this.getAllBrand();
    });
    // Search
    this.searchSubscription = this.searchSubject
      .pipe(debounceTime(800),
        distinctUntilChanged(),
        tap(() => {
          this.isLoading = true; // Show the loader when the API call is initiated
        })).subscribe(searchKey => {
          // Call the API with the search key
          if (searchKey.length >= 3) {
            this.getBrandSearch(searchKey.trim());
          } else if (searchKey.trim() === '') {
            // this.isLoading = true; // Hide the loader if the search key is less than the threshold
            this.getAllBrand();
          } else {
            this.isLoading = false; // Hide the loader if the search key is less than the threshold
          }
        });
  }

  getAllBrand() {
    this.productService.getBrandList({
      data:{}, 
      userId: this.currentUser.id, 
      size: this.partyPageSize, 
      page: (this.currentPageNo + 1), 
      search:'',
      isActive: '' 

 
    }).then((res: any) => {
      this.productBrand = res.data.pageData;
      this.p = res.data.currentPage;
      this.totalrow = res.data.totalrows;
      this.toatlPages = res.data.totalPages;
    })
  }

  openBrandForm() {
    const dialogRef: MatDialogRef<AddBrandComponent> = this.dialog.open(AddBrandComponent, {
      width: '500px',
      height: 'auto',
      data: {},
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((result) => {
    });
  }

  editBrand(brandId) {
    const dialogRef: MatDialogRef<EditBrandComponent> = this.dialog.open(EditBrandComponent, {
      width: '500px',
      height: 'auto',
      data: { brandId: brandId },
      disableClose: true,
    });
    dialogRef.afterClosed().subscribe((result) => {
    });
  }

  getBrandId(brandId) {
    this.brandId.push(brandId);
  }

  deleteBrand() {
    let body = {
      "brandId": this.brandId
    }
    this.productService.deleteBrand(body, this.currentUser.id).then((res) => {
      this.toastService.toastMsg({
        title: "Success",
        content: "Brand Deleted Successfully!!!",
      });
      this.getAllBrand()
      this.checkSelectsingle = false;
      this.upiCheckedId = [];
      this.checkSelect = false;
      this.allSelect = false;
      this.checkSingleArray = [];
      this.archiveIconfalse = false;
      this.allSelect = false;
      this.checkSelectsinglefalse = false;
      this.isChecked = false;
      this.checkSingleArray = [];

    }, (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went To Wrong.",
        });
      }
    }
    );
  }

  isActive(id, isActive) {
    const newStatus = !isActive; // Toggle between '1' and '0'
    const body = { "isActive": newStatus };
    this.productService.chnageBrandStatus(body, id).then((res: any) => {
      if (res.success) {
        if (newStatus) {
          this.toastService.toastMsg({
            title: "Success",
            content: "Status Has Changed To Active",
          });
          this.getAllBrand()
        } else {
          this.toastService.toastMsg({
            title: "Success",
            content: "Status Has Changed To Inactive",
          });
          this.getAllBrand()
        }
      }
    })
  }

  handlePage(e) {
    this.currentPageNo = e.pageIndex;
    this.pagesize = e.pageSize;
    if (this.productBrand.length > 0) {
      this.partyPageSize = e.pageSize;
      const searchInput = document.getElementById('searchInput') as HTMLInputElement;
      if (searchInput.value !== '') {
        this.onSearchInputChange(searchInput.value);
      } else {
        this.getAllBrand();
      }
      
    }
  }

  onChekedsingleCheck(e, expenseId) {
    if (e.target.checked) {
      this.upiCheckedId.push(expenseId);
    } else {
      const index = this.upiCheckedId.indexOf(expenseId);
      if (index !== -1) {
        this.upiCheckedId.splice(index, 1);
      }
    }
    // const checkArray: FormArray = this.form.get('checkArray') as FormArray;
    if (e.target.checked) {
      this.checkSingleArray.push(e.target.value);
    }
    else {
      this.checkSingleArray = [];
    }
  }

  getUPIcheckId(brandId) {
    this.brandId = brandId;
  }

  getSingleCheckedId(brandId) {
    this.brandId = brandId;
  }

  showDeleteButton(): boolean {
    return this.upiCheckedId.length > 0;
  }
  
  onChekMultiple($event) {
    let id = $event.target.value;
    this.isChecked = $event.target.checked;
    this.upiCheckedId = this.productBrand.map((item) => {
      if (id == -1) {
        if (this.isChecked == true) {
          this.archiveIconfalse = true;
          this.checkSelectsinglefalse = true;

          this.checkSelectsingle = true;
          return item.id;
        }
        else {
          this.archiveIconfalse = false;
          this.checkSelectsinglefalse = false;

          this.checkSelectsingle = false;
          this.upiCheckedId = [];
        }
      }
    })
  }

  onSearchInputChange(searchKey: string) {
    // Emit the search key to the searchSubject
    this.searchSubject.next(searchKey);
  }

  getBrandSearch(searchKey: string) {
    setTimeout(() => {
      this.productService.getBrandList({
        data:{}, 
        userId: this.currentUser.id, 
        size: this.partyPageSize, 
        page: (this.currentPageNo + 1), 
        search: (searchKey.toLowerCase()),
        isActive: '' 
      }).then((res) => {
        if (res.success) {
          this.productBrand = res.data.pageData;
          this.p = res.data.currentPage;
          this.totalrow = res.data.totalrows;
          this.toatlPages = res.data.totalPages;
        }
        // If row's and search key is not specified
        if (this.totalrow === 0 && !(searchKey === "")) {
          this.toastService.toastMsg({
            title: "Error",
            content: "Not Found!!!",
          });
        }
      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
        }
      })
      this.isLoading = false; // Hide the loader after the API call is completed
    }, 500);
  }

}
