import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormArray, FormBuilder, Validators } from '@angular/forms';
import { MatDialogRef } from '@angular/material/dialog';
import { CommonService } from 'src/app/core/services/common.service';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-add-custom-fields',
  templateUrl: './add-custom-fields.component.html',
  styleUrls: ['./add-custom-fields.component.scss']
})
export class AddCustomFieldsComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  customForm: any;
  categoryList: any;
  submitted: boolean = false;
  types: string[] = ['text', 'dropdown'];

  constructor(private formBuilder: FormBuilder, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,private productService: ProductService,
    public dialogRef: MatDialogRef<AddCustomFieldsComponent>, private commonService: CommonService) { }

  ngOnInit(): void {
    this.customForm = this.formBuilder.group({
      customFieldName: ["", [Validators.required]],
      type: ['text', Validators.required],
      dropdownOptions: this.formBuilder.array([]) // Initialize as an empty array
    })
    
    // Add one dropdown option initially
    this.dropdownOptions.push(this.formBuilder.control(''))
  }

  // Getters for easier access to form controls
  get dropdownOptions() {
    return this.customForm.get('dropdownOptions') as FormArray;
  }

  get f() {
    return this.customForm.controls;
  }

  postCustomFields(){
    this.submitted = true;
    const optionsArray = this.customForm.value.dropdownOptions.map((option) => {
      return {
        id: ``, // use the index as the id
        optionName: option ? option : '',
      };
    });
    console.log(this.customForm)
    if (this.customForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    }
    else {
      this.submitted = true;
      const body = {
        id: '',
        fieldName: this.customForm.value.customFieldName,
        type: this.customForm.value.type,
        userId: this.currentUser.id,
        isActive: true,
        options: optionsArray ? optionsArray : [],
      };

      this.productService.postCustomFields(body).then((res: any) => {
        if(res.success){
          this.commonService.notifyDataAdded();
          this.toastService.toastMsg({
            title: "Success",
            content: "Customize field added successfully"
          })
          this.close();
        }
      },
      (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
        }
      });
    }
  }

  // Add a new option to the dropdownOptions array
  addDropdownOption() {
    this.dropdownOptions.push(this.formBuilder.control('', Validators.required));
  }

  // Remove an option from the dropdownOptions array
  removeDropdownOption(index: number) {
    this.dropdownOptions.removeAt(index);
  }

  close(){
    this.dialogRef.close();
  }

}
