import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-edit-brand',
  templateUrl: './edit-brand.component.html',
  styleUrls: ['./edit-brand.component.scss']
})
export class EditBrandComponent implements OnInit {
  brandForm: any;
  public currentUser = JSON.parse(localStorage.getItem("currentUser"));
  submitted: boolean = false;
  brandId: any;
  constructor(private formBuilder: FormBuilder, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private productService: ProductService,
    public dialogRef: MatDialogRef<EditBrandComponent>, private router: Router, @Inject(MAT_DIALOG_DATA) public brandData: any,) { }

  ngOnInit(): void {
    this.brandId = this.brandData.brandId
    this.getBrandById()
    this.brandForm = this.formBuilder.group({
      brandName: ["", [Validators.required]],
    })
  }
  getBrandById() {
    this.productService.getBrandListById({}, this.currentUser.id, this.brandId).then((res: any) => {
      if (res) {
        this.brandForm.patchValue({
          brandName: res.data.data.name
        })
      }
    })

  }

  clearInput() {
    this.dialogRef.close();
  }
  get f() {
    return this.brandForm.controls;
  }

  addBrand() {
    this.submitted = true;
    if (this.brandForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    }
    else {
      this.submitted = true;
      let body = {
        "id": this.brandId.toString(),
        "name": this.brandForm.controls.brandName.value,

        "userId": this.currentUser.id,

      }
      this.productService.postBrand(body).then((res) => {
        if (res) {
          this.toastService.toastMsg({
            title: "Success",
            content: "Brand Updated Successfully!!!",
          });
          this.productService.notifyProductAdded();
          this.dialogRef.close();
        }

      }, (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went To Wrong.",
          });
        }
      })
    }
  }
}
