import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from '@angular/material/dialog';
import { CommonService } from 'src/app/core/services/common.service';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-edit-sub-category',
  templateUrl: './edit-sub-category.component.html',
  styleUrls: ['./edit-sub-category.component.scss']
})
export class EditSubCategoryComponent implements OnInit {
  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  subCategoryFrom: any;
  subCategoryId: any;
  categoryList: any;
  submitted: boolean = false;


  constructor(private formBuilder: FormBuilder, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe,private productService: ProductService,
    public dialogRef: MatDialogRef<EditSubCategoryComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private commonService: CommonService) { 
      this.subCategoryId = this.data.subCategoryId;
    }

  ngOnInit(): void {
    this.subCategoryFrom = this.formBuilder.group({
      subCategoryName: ["", [Validators.required]],
      category: ["", [Validators.required]],
    })
    this.getSubCategoryById()
    this.getAllCategory()
  }

  getAllCategory(){
    this.productService.getCategory({
      data:{}, 
      userId: this.currentUser.id, 
      size: 1000, 
      page: (1), 
      search:'', 
      isActive: '' 
    }).then((res: any) => {
      if(res.success){
        this.categoryList = res.data.pageData;
      }
    },
    (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
      }
    })
  }

  getSubCategoryById() {
    this.productService.getSubCategoryById({}, this.currentUser.id, this.subCategoryId).then(res => {
      if(res.success) {
        const subCategoryList = res.data.data
        this.subCategoryFrom.patchValue({
          subCategoryName: subCategoryList.name,
          category: subCategoryList.categoryId
        });
      }
    },
    (err) => {
      if (err.error.expose) {
        this.toastService.toastMsg({
          title: "Error",
          content: this.titleCasePipe.transform(err.error.error_message),
        });
      }
      else {
        this.toastService.toastMsg({
          title: "Error",
          content: "Something Went Wrong.",
        });
      }
      // Handle error.
    })
  }
  get f() {
    return this.subCategoryFrom.controls;
  }
  editCategory() {
    this.submitted = true;
    if (this.subCategoryFrom.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    }
    else {
      this.submitted = true;
      const newCategory = {
        id: `${this.subCategoryId}`, // Check if the server requires an ID or if it's auto-generated
        name: this.subCategoryFrom.value.subCategoryName,
        userId: this.currentUser.id,
        categoryId: `${this.subCategoryFrom.value.category}`,
      };

      this.productService.postSubCategory(newCategory).then((res: any) => {
        if (res.success) {
          this.commonService.notifyDataAdded();
          this.toastService.toastMsg({
            title: "Success",
            content: "Sub Category Updated Successfully"
          })
          this.close();
        }
      },
        (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
          }
          else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went Wrong.",
            });
          }
        });
    }
  }

  close(){
    this.dialogRef.close();
  }
}
