import { TitleCasePipe } from '@angular/common';
import { Component, Inject, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ProductService } from 'src/app/core/services/product.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';
import { CommonService } from 'src/app/core/services/common.service';

@Component({
  selector: 'app-edit-category',
  templateUrl: './edit-category.component.html',
  styleUrls: ['./edit-category.component.scss']
})
export class EditCategoryComponent implements OnInit {

  public currentUser = JSON.parse(localStorage.getItem("currentUser"))
  categoryForm: any;
  categoryId: any;
  submitted: boolean = false;

  constructor(private formBuilder: FormBuilder, private toastService: ToastNotificationService,
    private titleCasePipe: TitleCasePipe, private productService: ProductService,
    public dialogRef: MatDialogRef<EditCategoryComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any, private commonService: CommonService) {
    this.categoryId = this.data.categoryId;
  }

  ngOnInit(): void {
    this.categoryForm = this.formBuilder.group({
      categoryName: ["", [Validators.required]],
    })
    this.getCategory()
  }

  get f() {
    return this.categoryForm.controls;
  }

  getCategory() {
    this.productService.getCategory({
      data:{}, 
      userId: this.currentUser.id, 
      size: 1000, 
      page: (1), 
      search:'', 
      isActive: '' 
    }).then(res => {
      if (res.success) {
        const categoryList = res.data.pageData
        // Find the category by ID
        const foundCategory = categoryList.find(category => category.id === this.categoryId);
        if (foundCategory.categoryName) {
          this.categoryForm.patchValue({
            categoryName: foundCategory.categoryName
          });
        }
      }
    },
      (err) => {
        if (err.error.expose) {
          this.toastService.toastMsg({
            title: "Error",
            content: this.titleCasePipe.transform(err.error.error_message),
          });
        }
        else {
          this.toastService.toastMsg({
            title: "Error",
            content: "Something Went Wrong.",
          });
        }
        // Handle error.
      })
  }

  editCategory() {
    this.submitted = true;
    if (this.categoryForm.invalid) {
      this.toastService.toastMsg({
        title: "Error",
        content: "Fill All Required Fields.",
      });
      return false;
    }
    else {
      this.submitted = true;
      const newCategory = {
        id: `${this.categoryId}`, // Converts categoryIdStr to string if it's not already
        categoryName: this.categoryForm.value.categoryName,
        // isDefault: true, // Adjust these values as needed
        userId: this.currentUser.id,
        isActive: true,
      };

      this.productService.postCategory(newCategory).then((res: any) => {
        if (res.success) {
          this.commonService.notifyDataAdded();
          this.toastService.toastMsg({
            title: "Success",
            content: "Category Updated Successfully"
          })
          this.close();
        }
      },
        (err) => {
          if (err.error.expose) {
            this.toastService.toastMsg({
              title: "Error",
              content: this.titleCasePipe.transform(err.error.error_message),
            });
          }
          else {
            this.toastService.toastMsg({
              title: "Error",
              content: "Something Went To Wrong.",
            });
          }
        })
    }
  }

  close() {
    this.dialogRef.close();
  }
}
